.app {
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 4rem auto 4rem;
  gap: 0 0;
  grid-auto-flow: row;
  grid-template-areas:
    "load-button-up"
    "trips-list"
    "load-button-down";
  @media (min-width: $media-md) {
    grid-template-rows:112px 73px 1fr 73px;
    grid-template-areas:
    "page-title"
    "load-button-up"
    "trips-list"
    "load-button-down";
  }
}

.page-title {
  grid-area: page-title;
  display: none;
  @media (min-width: $media-md) {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 41px 62px;
    h1{
      display: block;
      margin-right: 12px;
      padding-bottom: 5px;
    }
  }
}

.load-button-up {
  grid-area: load-button-up;
}

.trips-list {
  grid-area: trips-list;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y:auto;
  padding: 0 18px;
  @media (min-width: $media-md) {
    padding: 0 62px;
  }
}

.load-button-down {
  grid-area: load-button-down;
}
